/* LotoMania styles.css */

.card {
  background-color: #ffffff;
  border-radius: 5px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  padding: 10px;
  width: 84%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}
.menu {
  display: flex;
  gap: 10px;
  justify-content: center;
  margin-top: 20px;
}

/* Botões Modernos */
.menuButton {
  font-family: 'Arial', sans-serif;
  font-size: 10px;
  font-weight: bold;
  padding: 10px 20px;
  border: 1px solid transparent;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  outline: none;
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* Botão Principal */
.primary {
  background-color: #fff; /* Cor do botão */
  color: #585858;
  border-color: #585858;
}

.primary:hover {
  background-color: #fff; /* Cor ao passar o mouse */
  color: #F78100; /* Texto em verde */
  border-color: #F78100;
}

/* Botão Secundário */
.secondary {
  background-color: #fff; /* Cor do botão */
  color: #585858;
  border-color: #585858;
}
.secondary:hover {
  background-color: #fff; /* Cor ao passar o mouse */
  color: #F78100; /* Texto em verde */
  border-color: #F78100;
}

.title {
  color: #F78100;
  font-size: 24px;
  margin-top: 20px;
}

.select {
  margin-top: 20px;
  padding: 10px;
  border-radius: 5px;
  font-size: 12px;
  border: 1px solid #585858;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.numberBox{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 80%;
  gap: 4px;
}

.numberItem {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #F78100;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: 2px;
  font-size: 16px;
}

.image {
  width: 60px;
  /* Define a largura para 150px */
  height: auto;
  /* Ajusta a altura proporcionalmente */
  border-radius: 8px;
  /* Opcional: bordas arredondadas */
}

h1 {
  margin-bottom: -20px;
}

.brand {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: #fff;
  margin-top: 0px;
  margin-bottom: 20px;
}

