body {
  background-color: #5f5f5f;
  display: flex;
  /* Define o layout como flexbox */
  justify-content: center;
  /* Centraliza horizontalmente */
  align-items: center;
  /* Centraliza verticalmente */
  /* Garante que o body ocupe toda a altura da tela */
  margin: 0;
  /* Remove as margens padrão do body */
  font-family: 'Roboto', sans-serif;
  /* Aplica Roboto como fonte padrão */

}
.container{
  background-color: #fafafa !important;
  /* Adicione !important se necessário */
  width: 400px;
  padding: 1px;
  display: flex;
}
.app {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  /* Ocupa a altura total da janela */
}


/* Botão do Menu Hamburger */
.menu-toggle {
  display: none;
  background-color: #007BFF;
  color: white;
  font-size: 20px;
  border: none;
  padding: 10px;
  cursor: pointer;
  border-radius: 5px;
  position: fixed;
  top: 10px;
  right: 30px;
  z-index: 1001;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

/* Menu Geral */
.menu {
  display: flex;
  gap: 10px;
  padding: 10px;
}

.menu-item {
  font-family: 'Arial', sans-serif;
  font-size: 14px;
  font-weight: bold;
  padding: 8px 15px;
  border-radius: 5px;
  text-decoration: none;
  color: white;
  text-align: center;
  transition: all 0.3s ease-in-out;
  text-transform: uppercase;
  cursor: pointer;
}

.inicio {
  background-color: #007BFF;
}

.lotofacil {
  background-color: #931d89;
}

.megasena {
  background-color: #209869;
}

.lotomania {
  background-color: #F78100;
}

.menu-item:hover {
  background-color: white;
  color: inherit;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

/* Menu Responsivo */
@media (max-width: 768px) {
  .menu-toggle {
    display: block;
  }

  .menu {
    flex-direction: column;
    background-color: rgba(255, 255, 255, 0.9); /* Fundo branco transparente */
    position: fixed;
    top: 0;
    left: 0;
    width: 60%; /* Largura do menu lateral */
    height: 100%;
    padding: 20px;
    z-index: 1000;
    transform: translateX(-100%);
    transition: transform 0.3s ease-in-out, background-color 0.3s;
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.2);
  }

  .menu.open {
    transform: translateX(0);
  }

  /* Estilo base para os itens do menu */
.menu-item {
  color: white; /* Texto branco por padrão */
  text-decoration: none;
  display: block;
  padding: 10px 20px;
  border-radius: 5px;
  transition: all 0.3s ease-in-out;
  text-transform: uppercase;
}

/* Hover para todos os itens */
.menu-item:hover {
  color: white; /* Texto branco no hover */
  transform: scale(1.05); /* Aumenta ligeiramente o item */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

/* Estilos específicos para cada item no hover */
.menu-item.inicio:hover,
.menu-item.inicio.active {
  background-color: #007BFF; /* Cor azul para Início */
  color: white; /* Garante que o texto permaneça visível */
}

.menu-item.lotofacil:hover,
.menu-item.lotofacil.active {
  background-color: #931d89; /* Cor roxa para LotoFácil */
  color: white; /* Garante que o texto permaneça visível */
}

.menu-item.megasena:hover,
.menu-item.megasena.active {
  background-color: #209869; /* Cor verde para MegaSena */
  color: white; /* Garante que o texto permaneça visível */
}

.menu-item.lotomania:hover,
.menu-item.lotomania.active {
  background-color: #F78100; /* Cor laranja para LotoMania */
  color: white; /* Garante que o texto permaneça visível */
}

/* Adiciona borda lateral ao item ativo */
.menu-item.active {
  border-left: 20px solid rgb(17, 2, 2); /* Borda lateral para destaque */
  font-weight: bold; /* Negrito para o item ativo */
}

/* Estilos específicos para cada item */
}

/* Overlay */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 999;
}


/*FIM MENU*/
.content {
  flex: 1;
  /* O conteúdo ocupa todo o espaço restante */
  display: flex;
  /* Importante para alinhar os filhos */
  justify-content: center;
  /* Centraliza horizontalmente */
  align-items: center;
  /* Centraliza verticalmente */
  background-color: #ffffff;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #931d89;
  height: 100%;
}

