
.list-container {
  flex: 1; /* Permite que a lista ocupe o espaço restante */
  overflow-y: auto; /* Adiciona scroll à lista */
  background-color: transparent;
  padding-bottom: 120px; /* Espaço para o botão flutuante */
}

.content {
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card-saved {
  background-color: #ffffff;
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.text {
  font-size: 16px;
  color: #931d89;
  margin-bottom: 10px;
}

.number-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.number-circle {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #931d89;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 14px;
}

.share-button {
  background: none;
  border: none;
  color: #ff1f1e;
  cursor: pointer;
  margin-top: 10px;
}

.share-button .material-icons {
  font-size: 12px;
}

.line {
  border: none;
  height: 1px;
  background-color: #ccc;
  margin: 10px 0;
}

.button.limpar {
  position: fixed;
  bottom: 40px;
  z-index: 10; /* Garante que fique acima de outros elementos */
}

.resultText{
  font-weight: bold;}


